import React, { Component } from "react";
import {Container, Typography } from "@material-ui/core"
import "./App.css";
import 'fontsource-roboto';
import CadastroGeral from "./components/FormularioCadastro/CadastroGeral";
import {validarCPF, validarSenha} from './models/cadastro'
import Validacoes from "./context/ValidacaoCadastro";

class App extends Component {
  render() {
    return (
      <Container component="article" maxWidth="sm">
        <Typography variant="h3" component="h1" align="center" >Formulário de cadastro</Typography>
        <Validacoes.Provider value={{cpf: validarCPF, senha: validarSenha, nome: validarSenha}}>        
          <CadastroGeral aoEnviar={aoEnviarForm}/>
        </Validacoes.Provider>
      </Container>
    );
  }
}

function aoEnviarForm(dados){
  console.log(dados);
}

export default App;
