import React, { useEffect, useState } from "react";
import DadosPessoais from './DadosPessoais'
import DadosUsuario from './DadosUsuario'
import DadosEntrega from './DadosEntrega'   
import { Step ,StepLabel, Stepper, Typography } from "@material-ui/core";

function CadastroGeral({aoEnviar, validacoes}) {
    const [etapaAtual, setEtapaAtual] = useState(0)
    const [dadosGeral, setDados] = useState({});
    const formulario = [
    <DadosUsuario aoEnviar={coletarDados} validacoes={validacoes}/>, 
    <DadosPessoais aoEnviar={coletarDados} validacoes={validacoes}/>, 
    <DadosEntrega aoEnviar={coletarDados} validacoes={validacoes}/>,
    <Typography align="center">Cadastro finalizado!!!</Typography>
    ];
    useEffect(() => {
        if(etapaAtual === formulario.length-1){
            aoEnviar(dadosGeral);
        }
    })

    function coletarDados(dados) {
        setDados({...dadosGeral, ...dados })
        proximo();
    }

    function proximo() {
        setEtapaAtual(etapaAtual+1)
    }

  return (
      <>
      <Stepper activeStep={etapaAtual}>
          <Step><StepLabel>Login</StepLabel></Step>
          <Step><StepLabel>Pessoal</StepLabel></Step>
          <Step><StepLabel>Entrega</StepLabel></Step>
          <Step><StepLabel>Finalizado</StepLabel></Step>
      </Stepper>
      {formulario[etapaAtual]}
      </>
  )
}

export default CadastroGeral;
